.mmProgress {
  position: relative;
  width: 100%;
  height: 2Px;
  padding: 9Px 0;
  margin: 0 10Px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.mmProgress .mmProgress-bar {
  height: 2Px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.15);
}
.mmProgress .mmProgress-outer {
  position: absolute;
  top: 50%;
  left: 0;
  display: inline-block;
  width: 0;
  height: 2Px;
  margin-top: -1px;
  background-color: rgba(255, 255, 255, 0.2);
}
.mmProgress .mmProgress-inner {
  position: absolute;
  top: 50%;
  left: 0;
  display: inline-block;
  width: 0;
  height: 2Px;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: blue;
}
.mmProgress .mmProgress-inner .mmProgress-dot {
  position: absolute;
  top: 50%;
  right: -9Px;
  width: 18Px;
  height: 18Px;
  border-radius: 50%;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  background-color: #fff;
}
.mmProgress .mmProgress-inner .mmProgress-dot:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 5Px;
  height: 5Px;
  border-radius: 50%;
  background-color: blue;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}



